@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    color:rgb(26, 26, 26);
    font-family: 'Oswald', sans-serif;
}
.fixed-side-bar{
    position: -webkit-sticky !important;
    position: sticky !important;
    top:0;
    left: 0;
}
.invisible-blk{
    display: none;
}
.side-bar{
    height: 100vh;
    box-shadow: 4px 5px 8px -2px rgba(145,145,145,1);
}
.nav-pills .nav-link{
    color:rgb(26, 26, 26);
}

@media only screen and (max-width: 767px){
    .side-bar{
        display: none;
    }
    .invisible-blk{
        background-color: white;
        display: block;
        position: -webkit-sticky !important;
        position: sticky !important;
        top:0;
        left: 0;
        z-index:100;
    }
}

.btn-primary ,.nav-pills .nav-link.active{
    background-color: #d6186d !important;
}

.btn-primary{
    border-color: #d6186d !important;
}
.tab-content-wrapper{
    background-color: #f0f0f0;
}

.chart-wrapper{
    background-color: white;
    padding:20px;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    box-shadow: 4px 5px 8px -2px rgba(145,145,145,1);
}

.navigate{
    background-color: white;
    border-radius: 5px;
    width: 100%;
}

.dropdown-menu.show {
    height: 200px !important;
    overflow-y: auto;
}

.leaflet-container{
    width: 81vw;
    height: 100vh;
}
.search-result{
    width: 100%;
    position: absolute;
    z-index: 100;
    background-color: white;   
}
.search-wrapper{
    position: relative;
    width: 100%;
    height: 0;
}

.with-data{
    background-color: #d6186d !important;
    color:white !important;
}
.page-link{
    color: #d6186d !important;
}
.page-item.active .page-link{
    border-color: #d6186d !important;
    background-color: #d6186d !important;
    color:white !important;
}

.cluster-marker {
    color: #fff;
    background: #d6186d;
    border-radius: 50%;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .poi-marker {
    background: none;
    border: none;
  }

  .additional-data{
      font-size: 1.2rem;
      font-weight: bold;
  }

  .navigate{
      width: 100%;
  }
